import React, {useState} from "react"
import * as button from "../styles/button.module.css"

export default function PressableButton({text, image, changeTo}) {
    const [primary, secondary] = useState(text)

    return <div className={button.button} onClick={() => secondary(changeTo)}>
        <img src={image}></img>
        <span>{primary}</span>
    </div>
}
